:root {
  --main-color:#678983;
  --dark-color:#181D31;
  --light-color:#E6DDC4;
  --second-color:#f7e7b5;
  --bounceEasing: cubic-bezier(0.51, 0.92, 0.24, 1.15);
}
@font-face {
  font-family:"Gummy";
  src: url(./Gummy\ Bears.ttf);
}

body,html{
  font-family: "Gummy", Times, serif;
  min-height: 100vh;
  max-height: 100vh;
}
* { 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
#root {
  display: flex;
  flex-direction: column;
  min-width: 100vh;
  min-height: 100vh;
  max-height: 100vh;
}

#like:hover{
 color: var(--light-color);
 background-color: transparent;
}

/* Splash Screen */ 
#splash-screen {
  flex: 1;
  display: flex;
  gap: 4rem;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(to bottom, #E6DDC4, #678983);
}
.logo{
  display:flex; 
  align-items:center;
  text-decoration: none;
  color: white;
}
.logo:hover{
  color: var(--light-color);
}
#main-splash{
  display: flex;
  gap: 2rem;
}
.splash-header{
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0.3;
}
.splash-bottom{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  flex: 0.3;
}
.splash-text{
  flex: 0.5;
}
/* Home Screen */
#home-screen{
  min-height: 100vh;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-image: linear-gradient(to bottom, #E6DDC4, #678983);
}

/*Main Section */
.home-main{
  display: flex;
  flex: 1;
  max-height: 730px;
  justify-content:center;
}
.home-footer{
  display: flex;
  justify-content: center;
  align-items: center;
}
/* List Area */
.list-area{
  flex: 1;
  max-height: 730px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
}

/* List */ 
#user-list{
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 15px;
  padding: 1;
  width: 100%;
  font-size: 24pt;
  background-color:#e4f0ee;
  border-radius:5px;
}

/* workspace */ 
.workspace{
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  max-height: 420px;
}
#list-selector-list {
  flex: 1;
  max-height: 430px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
  padding: 1rem;
  gap: 2rem;
}

/*published area */ 
#published-list{
  flex: 1;
  max-height: 430px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
  padding: 1rem;
  border-radius: 5px;
  
}
.song-card{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin-right: 2rem;
  border-radius: 5px;
  box-shadow: 15px 15px 0 -4px transparent,
  5px 6px rgb(73, 93, 99);
  background-color: var(--light-color);
  color: black;
}
.published-card{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin-right: 2rem;
  border-radius: 5px;
  
  
}
.published-card:hover{
  cursor: pointer;
  color: #e4f0ee;
}
.song-card:hover{
  cursor: pointer;
  background-color: #e4f0ee;
  color: #071935;
}




#edit-toolbar {
  background-color: transparent;
  display: flex;
  justify-content: space-between;
}


/* Video Player & Comments */
.player-comments{
  position: relative;
  display: flex;
  flex: 1;
  max-height: 730px;
  flex-direction: column;
  padding: 5px;
}

/* Video Player*/
.video-player{
  position: absolute;
  top:10%;
  bottom:2%;
  left:1%;
  right: 2%;
  display: flex;
  flex-direction: column;
  
 }

.video-area{
  display: flex;
  flex: 1;
  min-height: 300px;
  background-color:#333333 ;
  border-radius: 3px;
}
.video-actions{
  display: flex;
  flex-direction: column;
  background-color:var(--light-color);
  color: #071935;
  border-radius: 4px;
}
.video-info{
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-height:150px;
  max-height:170px;
}

.video-buttons{
  align-self: center;
}

/* Video Player End */

/*Comments */ 

.comments{
  position: absolute;
  top:6%;
  bottom:2%;
  left:2%;
  right: 2%;
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0.5rem;
  gap: 0.5rem;
}

.comments-area{
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
  gap: 0.3rem;
  border-radius: 10px;
}
.comment{
  display: flex;
  background-color: var(--light-color);
  margin-right: 1rem;
  margin-top:0.3rem;
  margin-bottom:0.3rem;
  padding: 0.5rem;
  border-radius: 4px;
  gap: 0.4rem;
  box-shadow: 15px 15px 0 -4px transparent,
  5px 6px #071935;
  border: #071935 1.5px solid;
}

/* AppTools */
#AppTools{
  display: flex;
  color:black;
  justify-content: space-between;
  background-color:var(--light-color);
}
.tool-icons{
 display: flex;
 gap: 2rem;
}
#add-list-button {
  float: left;
}
input {
  font-size: 20pt;
}
#error-style{
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  align-self: center;
}
#alert-style{
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

